// stripeService.ts
import { loadStripe } from '@stripe/stripe-js';
import { getStripeProductKeys } from './firebaseService';

let stripePromise: Promise<any> | null = null;

export const getStripe = () => {
  if (!stripePromise) {
    stripePromise = loadStripe(process.env.VITE_STRIPE_PUBLIC_KEY);
  }
  return stripePromise;
};

// Trae el priceId real desde Firestore
export const getProductKey = async (productType: 'remove_ads' | 'premium_phrases'): Promise<string> => {
  try {
    const productKeys = await getStripeProductKeys(); // desde Firestore
    const key = productType === 'remove_ads' 
      ? productKeys.price_remove_ads 
      : productKeys.price_premium_phrases;

    if (!key.startsWith('price_')) {
      console.error('El priceId no es válido para Stripe:', key);
      throw new Error('ID de precio inválido');
    }

    return key;
  } catch (error) {
    console.error('Error obteniendo clave de producto:', error);
    throw new Error('No se pudo obtener el producto de Stripe');
  }
};

export const createCheckoutSession = async (priceId: string) => {
  try {
    const stripe = await getStripe();
    
    // Stripe Checkout directo desde frontend usando priceId
    const { error } = await stripe.redirectToCheckout({
      lineItems: [{ price: priceId, quantity: 1 }],
      mode: 'payment',
      successUrl: window.location.href,
      cancelUrl: window.location.href,
    });

    if (error) {
      console.error('Error redirigiendo a Stripe Checkout:', error);
      throw new Error(error.message);
    }
  } catch (error) {
    console.error('Error creando sesión de checkout:', error);
    throw new Error('No se pudo procesar el pago. Inténtalo más tarde.');
  }
};

export const handleRemoveAdsPayment = async () => {
  try {
    const priceId = await getProductKey('remove_ads');
    await createCheckoutSession(priceId);
    return true;
  } catch (error) {
    console.error('Error procesando pago Remove Ads:', error);
    return false;
  }
};

export const handlePremiumPayment = async () => {
  try {
    const priceId = await getProductKey('premium_phrases');
    await createCheckoutSession(priceId);
    return true;
  } catch (error) {
    console.error('Error procesando pago Premium:', error);
    return false;
  }
};
